
#app {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.app-container {
    max-width: 560px;
}

.app-form {
    position: relative;
    width: 100%;
    margin-bottom: 20px;
}

.app-input {
    width: 560px;
    padding: 18px 60px;
    background-image: url('./assets/img/search.svg');
    background-repeat: no-repeat;
    background-position: 23px center;
    background-size: 15px;
    background-color: #1f2a48;
    border: 1px solid #003670;
    border-radius: 10px;
    font-family: 'Source Code Pro', monospace;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    color: var(--main-color);
}

.app-input::placeholder {
    color: var(--main-color);
}

.app-input:active,
:hover,
:focus {
    outline: 0;
    outline-offset: 0;
}

.app-form_btn {
    position: absolute;
    right: 8px;
    top: 7px;
    background-color: #007afe;
    padding: 11px 24px;
    border: 1px solid #3797ff;
    border-radius: 10px;
    box-shadow: 0px 10px 15px rgba(0, 122, 254, 0.4);
    font-family: 'Source Code Pro', monospace;
    font-weight: 500;
    font-size: 18px;
    line-height: 23px;
    color: #fff;
    cursor: pointer;
}

.app-form_btn:disabled,
.app-form_btn[disabled] {
    opacity: 0.5;
}

.app-user {
    padding: 35px;
    background-color: #1f2a48;
    border: 1px solid #003670;
    border-radius: 10px;
}

.app-user_info {
    display: flex;
    margin-bottom: 25px;
}

.app-user_image {
    margin-right: 24px;
}

.app-user_image img {
    border-radius: 50%;
}

.app-user_name {
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 25px;
    line-height: 31px;
    color: #fff;
}

.app-user_name span {
    display: block;
    font-weight: 500;
    font-size: 18px;
    line-height: 23px;
    color: #007afe;
}

.app-user_about {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
}

.app-user_stats {
    display: flex;
    padding: 14px 10px;
    margin-bottom: 22px;
    list-style: none;
    border-radius: 10px;
    background-color: #141c2f;
}

.app-user_stats-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 28px;
    font-size: 16px;
    line-height: 20px;
    border-right: 1px solid #1f2a48;
}

.app-user_stats-item:last-child {
    border-right: 0;
}

.app-user_stats-item span {
    font-weight: bold;
    font-size: 20px;
    line-height: 25px;
    color: #ffffff;
}

.app-user_location {
    list-style: none;
}

.app-user_location-item {
    padding-left: 26px;
    margin-bottom: 17px;
    font-size: 15px;
    line-height: 19px;
    letter-spacing: -0.02em;
    background-image: url('./assets/img/location.svg');
    background-position: left center;
    background-repeat: no-repeat;
    background-size: 16px;
}

.app-user_location-item:last-child {
    background-image: url('./assets/img/link.svg');
}

.app-user_location-item a {
    text-decoration: none;
    color: var(--main-color);
    transition: color 0.3s ease-in-out;
}

.app-user_location-item a:hover {
    color: #fff;
}

@media (max-width: 560px) {
    .app-container {
        margin: 0 12px;
    }
    .app-input {
        width: 100%;
    }
    .app-user_info {
        flex-direction: column;
    }
    .app-user_stats {
        flex-direction: column;
    }
}
